.carousel-material {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 250px;
  margin: 0 auto;
  @media (min-width: 425px) {
    height: 350px;
  }
  @media (min-width: 1200px) {
    width: 70%;
    perspective: 1200px;
    transform-style: preserve-3d;
    transform-origin: 0% 50%;
  }
  &.carousel-slider {
    top: 0;
    left: 0;
    .carousel-fixed-item {
      &.with-indicators {
        bottom: 68px;
      }
      position: absolute;
      left: 0;
      right: 0;
      bottom: 20px;
      z-index: 1;
    }
    .carousel-material-item {
      width: 100%;
      height: 100%;
      min-height: 150px;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  .carousel-material-item {
    visibility: hidden;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    @media (min-width: 1200px) {
      width: 80%;
    }
    & > img {
      width: 100%;
    }
  }
  // Materialbox compatibility
  &.scrolling .carousel-material-item .materialboxed,
  .carousel-material-item:not(.active) .materialboxed {
    pointer-events: none;
  }
}
