.section-cards-top {
  background-color: $white;
  padding-top: 2rem;
  padding-bottom: 2rem;
  @media (min-width: 1200px) {
    margin-top: -18rem;
  }
  .cards-cuadradas {
    box-shadow: 0px 93px 33px -87px rgba(0,0,0,0.2);
    @media (max-width: 991.98px) {
      display: flex;
      flex-wrap: nowrap;
      overflow-x: auto;
      overflow-y: hidden;
      &::-webkit-scrollbar {
        display: none;
      }
    }
    .card {
      border: none;
      border-radius: 10px;
      transition: all .5s;
      margin: 0 1rem;
      border-radius: calc(.8rem - 1px);
      box-shadow: 0px 0px 16px 9px  rgba(142, 58, 252, 0.1);
      .card-header {
        border-radius: calc(.8rem - 1px) calc(.8rem - 1px) 0 0;
        background: radial-gradient(ellipse at center, rgba(131,106,245,1) 0%, rgba(131,106,245,1) 18%, rgba(69,36,203,1) 94%, rgba(255,255,255,1) 100%);
        text-align: center;
        color: $white;
        font-weight: 600;
        .iconos-top {
          height: 1.4rem;
          margin-right: .4rem;
        }
      }
      .col-izq {
        border-right: 2px solid #3432336b;
        display: flex;
        justify-content: center;
        align-items: center;
        .number-top {
          font-size: 1.2rem;
          color: $blue;
          font-weight: 600;
        }
      }
      .content-img {
        border-radius: 50%;
        img {
          border-radius: 50%;
        }
      }
      .details-card {
        .title-card {
          font-weight: 600;
          margin-bottom: 0;
        }
        .subtitle-card {
          margin-bottom: 0;
          font-size: .8rem;
          color: $gray-light;
        }
      }
      .col-sombreada {
        background-color: #EFEFEF;
      }
    }
    .card-izq {
      border-radius: calc(.8rem - 1px) calc(.8rem - 1px) calc(.8rem - 1px) 80px;
    }
    .card-der {
      border-radius: calc(.8rem - 1px) calc(.8rem - 1px) 80px calc(.8rem - 1px);
    }
  }
}