
.content-slider {
  position: relative;
  .carousel-material {
    .carousel-material-item {
      background-size: cover;
      background-position: center;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      transition: transform 0s ease-in-out;
      border-radius: 15px;
      .header-slider {
        background-color: $white;
        margin: 0;
        padding: 1rem;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        .badge {
          background-color: #ccc;
          padding: 0.5rem 2rem;
          border-radius: 50px;
          color: $blue;
          font: 1.2rem;
        }
        .title-slider {
          margin: 0;
        }
      }
      .content-info {
        position: relative;
        height: 100%;
        display: flex;
        align-items: flex-end;
        .footer-slider {
          width: 100%;
          background-color: rgba($color: $white, $alpha: 0.5);
          padding: 0.5rem;
          border-bottom-left-radius: 15px;
          border-bottom-right-radius: 15px;
          .text-slider {
            margin: 0;
            color: $white;
            font-size: 1rem;
            i {
              padding-right: 1rem;
              color: $green;
            }
          }
          .btn {
            padding-left: 1rem;
            padding-right: 1rem;
            font-size: 0.8rem;
          }
        }
      }
      img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
        z-index: -1;
        transition: .5s ease;
        object-fit: cover;
        object-position: top;
      }
      .placa-slider {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 15px;
        background: linear-gradient(180deg, rgba(255, 255, 255, 1) 0, rgba(255, 255, 255, 0.9) 20%, rgba(255, 255, 255, 0) 40%);
      }
    }
  }
  .carousel-material-prev,
  .carousel-material-next {
    width: 40px;
    height: 40px;
    padding: 0;
    border: 0;
    background: transparent;
    position: absolute;
    bottom: 0;
    left: 50%;
    opacity: 1;
    transition: all 0.3s;
    &:hover {
      opacity: 0.5;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  .carousel-material-prev {
    transform: translate(calc(-100% - 0.5rem), calc(100% + 0.5rem));
    @media (min-width: 1200px) {
      top: 50%;
      left: 12%;
      transform: translateY(-50%);
    }
  }
  .carousel-material-next {
    transform: translate(calc(0.5rem), calc(100% + 0.5rem));
    @media (min-width: 1200px) {
      top: 50%;
      left: initial;
      right: 12%;
      transform: translateY(-50%);
    }
  }
}
