.section-eventos {
  padding-top: 1rem;
  padding-bottom: 5rem;
  .carousel-indicators {
    bottom: -5rem;
  }
  .carousel-indicators [data-bs-target] {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: $blue;
  }
  .carousel-control-prev {
    left: -100px;
  }
  .carousel-control-next {
    right: -100px;
  }
}