.section-welcome {
  background-image: url(../images/bg-welcome.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  padding-top: 100px;
  padding-bottom: 5rem;
  @media (min-width: 992px) {
    padding-top: 150px;
  }
  @media (min-width: 1200px) {
    padding-bottom: 0;
  }
  .sombra-circle {
    width: 1px;
    height: 1px;
    margin: 0 auto;
    margin-top: 2rem;
    background: transparent;
    box-shadow: 0 0 150px 100px rgba($color: #000000, $alpha: 0.5);
    border-radius: 50%;
    transform: rotateX(86deg);
    @media (min-width: 768px) {
      box-shadow: 0 0 150px 200px rgba($color: #000000, $alpha: 0.5);
      transform: rotateX(87deg);
    }
    @media (min-width: 768px) {
      box-shadow: 0 0 200px 300px rgba($color: #000000, $alpha: 0.5);
      transform: rotateX(87deg);
    }
  }
  .title-btn-secciones {
    color: $white;
    margin-top: calc(1rem + 2rem);
    margin-bottom: 0;
    text-align: center;
  }
  .content-btn-secciones {
    text-align: center;
    margin-top: calc(3rem - 0.5rem);
    a {
      margin-top: 1rem;
      img {
        height: 1.2rem;
        margin-right: 0.5rem;
      }
    }
  }
  .border-arc {
    position: relative;
    width: 150%;
    height: 20rem;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    margin-top: 5rem;
    background: $white;
    border: 1px solid $white;
    left: 50%;
    transform: translateX(-50%);
  }
}